<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> VIP会员</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					VIP会员共：{{products.length}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="编号" width="90">
			</el-table-column>
			<el-table-column prop="headLogo" label="头像" width="130">
				<!-- 图片的显示 -->
				<template slot-scope="scope">
					<img :src="scope.row.headLogo" height="30" width="30" />
				</template>
			</el-table-column>
			<el-table-column prop="name" label="用户名称" min-width="100">
			</el-table-column>
			<el-table-column prop="balance" label="账户余额" min-width="100">
			</el-table-column>
			<el-table-column prop="email" label="用户邮箱" min-width="160">
			</el-table-column>
			<el-table-column prop="mobile" label="联系方式" min-width="120">
			</el-table-column>
			<el-table-column prop="createTime" label="注册日期" min-width="130">
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :current-page.sync="PageNum" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import Vue from 'vue'
	import util from '../../common/js/util'
	import {
		getcustomerdDelete,
		getcustomerlist
	} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					name: '',
					headLogo: '',
					email: '',
					mobile: ''
				},
				editorOption: {},
				type: [],
				region: [],
				time: '',
				products: [],
				total: 0,
				PageNum: 1,
				listLoading: false,
				sels: [], //列表选中列
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			//获取产品列表
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10
				};
				this.listLoading = true;
				//NProgress.start();
				getcustomerlist(para).then((res) => {
					this.PageNum = res.data.result.PageNum;
					this.total = res.data.result.total;
					this.oldproducts = res.data.result.result;
					let oldchildArr = []
					this.oldproducts.forEach(value => {
						if ( value.vipFlag == 'Y'){
							oldchildArr.push(value)
						}
					})
					this.products = oldchildArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			getphone() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10
				};
				this.listLoading = true;
				//NProgress.start();
				getcustomerlist(para).then((res) => {
					//this.total = res.data.result.total;
					this.pageNum= res.data.result.pageNum;
					this.listLoading = false;
					this.oldproducts = res.data.result.result;
					let oldchildArr = []
					this.oldproducts.forEach(value => {
						if (value.vipFlag== 'Y'){
							oldchildArr.push(value)
						}
					})
					this.newproducts = oldchildArr
					let childArr = []
					this.newproducts.forEach(value => {
						if (this.filters.telephone===value.mobile){
							childArr.push(value)
						}
					})
					this.products = childArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
