import Router from 'vue-router'
import Vue from 'vue'

Vue.use(Router)

import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import echarts from './views/charts/echarts.vue'

//产品管理
import products from './views/nav1/products.vue'
import zhuanli from './views/nav1/zhuanli.vue'
import sbqggl from './views/nav1/sbqggl.vue'
import zlqggl from './views/nav1/zlqggl.vue'


//用户管理
import users from './views/nav2/users.vue'
import vipusers from './views/nav2/vipusers.vue'
import yjfk from './views/nav2/yjfk.vue'


//订单管理
import sb_order from './views/nav3/sb_order.vue'
import zl_order from './views/nav3/zl_order.vue'
import fw_order from './views/nav3/fw_order.vue'
import sqvipgl from './views/nav3/sqvipgl.vue'
import txgl from './views/nav3/txgl.vue'

//商标服务管理
import sbfenl from './views/nav4/sbfenl.vue'

//新闻中心
import sbknow from './views/nav5/sbknow.vue'
import sbask from './views/nav5/sbask.vue'
import sbnew from './views/nav5/sbnew.vue'
import comnew from './views/nav5/comnew.vue'

//基础信息
import message from './views/nav6/message.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
//  //{ path: '/main', component: Main },
//  {
//      path: '/',
//      component: Home,
//      name: '大数据概览',
//      iconCls: 'el-icon-s-operation',//图标样式class
//      leaf: true,//只有一个节点
//      children: [
//      { path: '/', component: echarts, name: '大数据概览' },
//      ]
//  },
    {
        path: '/',
        component: Home,
        name: '产品管理',
        iconCls: 'el-icon-goods',//图标样式class
        leaf: false,//只有一个节点
        children: [
        { path: '/', component: products, name: '商标超市'},
        { path: '/zhuanli', component: zhuanli, name: '专利超市' },
        { path: '/sbqggl', component: sbqggl, name: '商标求购' },
        { path: '/zlqggl', component: zlqggl, name: '专利求购' },
        //{ path: '/protype', component: protype, name: '产品类型' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '用户管理',
        iconCls: 'el-icon-user',
        leaf: false,//只有一个节点
        children: [
            { path: '/users', component: users, name: '普通用户' },
            { path: '/vipusers', component: vipusers, name: 'VIP会员' },
            { path: '/yjfk', component: yjfk, name: '用户反馈' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '订单管理',
        iconCls: 'el-icon-tickets',
        leaf: false,//只有一个节点
        children: [
            { path: '/sb_order', component: sb_order, name: '商标订单' },
            { path: '/zl_order', component: zl_order, name: '专利订单' },
            { path: '/fw_order', component: fw_order, name: '服务订单' },
            { path: '/sqvipgl', component: sqvipgl, name: '升级VIP申请' },
            { path: '/txgl', component: txgl, name: '提现申请' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '商标服务管理',
        iconCls: 'el-icon-medal',
        leaf: true,//只有一个节点
        children: [
            { path: '/sbfenl', component: sbfenl, name: '商标服务管理' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '新闻管理',
        iconCls: 'el-icon-news',
        leaf: false,//只有一个节点
        children: [
            { path: '/sbknow', component: sbknow, name: '商标知识' },
            { path: '/sbask', component: sbask, name: '商标问问' },
            { path: '/sbnew', component: sbnew, name: '商标法律' },
            { path: '/comnew', component: comnew, name: '公司动态' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '基础信息',
        iconCls: 'el-icon-postcard',
        leaf: true,//只有一个节点
        children: [
            { path: '/message', component: message, name: '基础信息' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },
];

export default routes;



