<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> 国内商标</span>
				</el-form-item>
				<el-form-item>
				<el-select v-model="value" placeholder="请选择" :width="200">
					<el-option
							v-for="item in options"
							:key="item.value"
							:label="item.label"
							:value="item.value">
					</el-option>
				</el-select>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>

				<el-form-item style="float: right;">
					国内商标共：{{products.length}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="编号" width="80">
				<template slot-scope="scope">
					<span v-text="Number(scope.$index) + 1 + (PageNum - 1) * 10"></span>
				</template>
			</el-table-column>
			<el-table-column prop="bannerUrl" label="缩略图" width="130">
				<!-- 图片的显示 -->
				<template slot-scope="scope">
					<img :src="scope.row.bannerUrl" height="40" width="80" style="margin-top: 10px;" />
				</template>
			</el-table-column>
			<el-table-column prop="name" label="商标名称" min-width="130">
			</el-table-column>
			<el-table-column prop="customerMobile" label="联系方式" min-width="100">
			</el-table-column>
			<el-table-column prop="createTime" label="发布时间" min-width="130">
			</el-table-column>
			<el-table-column label="特价" width="130">
				<template slot-scope="scope">
					<el-button type="primary"  size="small" @click="handletejia(scope.$index, scope.row)"  v-if="scope.row.servicePrice ==null">设为特价标</el-button>
					<el-button type="danger"  size="small" @click="handleqxtejia(scope.$index, scope.row)"  v-else-if="scope.row.servicePrice ==1">取消特价标</el-button>
				</template>
			</el-table-column>
			<el-table-column label="编辑" width="90">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看</el-button>
				</template>
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--编辑界面-->
		<el-dialog title="商标详情" :visible.sync="editFormVisible">
			<el-form :model="editForm">
				<el-row class="ck_tab">
					<el-col :span="24">
						<el-form-item>
							<p><img :src="editForm.bannerUrl" width="414" height="200"  style="margin: 0 auto;"/></p>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">商标名称：</span>{{editForm.name}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">价格：</span>{{editForm.price}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">服务费：</span>{{editForm.servicePrice}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">卖家联系方式：</span>{{editForm.customerMobile}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">所属类别：</span>{{editForm.categorySec}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">初审公告期号：</span>{{editForm.firstNoticeNo}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">初审公告日期：</span>{{editForm.firstNoticeTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">适用范围：</span>{{editForm.applyRange}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">注册公告期号：</span>{{editForm.registerNoticeNo}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">注册公告时间：</span>{{editForm.registerNoticeTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">是否国内：</span>{{editForm.inlandFlag}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">商标归属区域：</span>{{editForm.areaCode}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">后期指定日期：</span>{{editForm.laterAppointTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">到期日：</span>{{editForm.expireTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">优先权时间：</span>{{editForm.priorityTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">国际注册时间：</span>{{editForm.internationRegisterTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">专用期限：</span>{{editForm.deadlineyear}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">是否共用：</span>{{editForm.publicFlag}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;">发布时间：</span>{{editForm.createTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</el-dialog>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		brandgetList,
		branddelete,
		brandupdate
	} from '../../api/api';
	export default {

		data() {
			return {
				filters: {
					name: '',
					bannerUrl: '',
					createTime: '',
					customerMobile: '',
				},
				options: [{
					value: '0',
					label: '全部商标'
				}, {
					value: '1',
					label: '每日特价标',
				}, {
					value: '2',
					label: '名标专区'
				}, {
					value: '3',
					label: '商标许可共享专区'
				}],
				value: '0',
				type: [],
				products: [],
				total: 0,
				PageNum:'1',
				listLoading: false,
				sels: [], //列表选中列
				editFormVisible: false, //编辑界面是否显示
				//编辑界面数据
				editForm: {
				},

			}
		},
		watch: {
          value(val){
          	this.value=this.value
			  this.getProducts(this.value)
		  }
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts(this.value);
			},
			handleRemove(files) {
				console.log(files);
			},
			handlePreview(files) {
				console.log(files);
			},

			//获取产品列表
			getProducts(value) {
				let para
				switch (Number(value)) {
					case 0:
						para = {
							pageNum: this.pageNum,
							pageSize: 10
						};
						break;
					case 1:
						para = {
							pageNum: this.pageNum,
							pageSize: 10,
							servicePrice: 1
						};
							break;

					case 2:
						para = {
							pageNum: this.pageNum,
							pageSize: 10,
							vipFlag:'Y'
						};
						break;
					case 3:
						para = {
							publicFlag:'Y',
							pageNum: this.pageNum,
							pageSize: 10,
						};
						break;
					default:
						para = {
							pageNum: this.pageNum,
							pageSize: 10,
						};
				}
				console.log(para)
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					//console.log(res.data.result);
					this.total = res.data.result.total;
					this.products = res.data.result.result;
					this.pageNum=res.data.result.pageNum;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					this.listLoading = false;
				});
			},
			getphone() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
					inlandFlag: 'Y'
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					//console.log(res.data.result);
					this.total = res.data.result.total;
					this.pageNum=res.data.result.pageNum;
					this.listLoading = false;
					this.newproducts = res.data.result.result;
					let childArr = []
					this.newproducts.forEach(value => {
						if (this.filters.telephone===value.customerMobile){
							childArr.push(value)
						}
					})
					this.products = childArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//取消特价标
			handleqxtejia: function(index, row) {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('取消设为特价标吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id,
						servicePrice: null,
						customerId: row.customerId,
					};
					brandupdate(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '取消成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},
			//设为特价标
			handletejia: function(index, row) {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认设为特价标吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id,
						servicePrice: 1,
						customerId: row.customerId,
					};
					brandupdate(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '设置成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = {
					name: row.name,
					price: row.price,
					servicePrice: row.servicePrice,
					categorySec: row.categorySec,
					firstNoticeNo: row.firstNoticeNo,
					bannerUrl: row.bannerUrl,
					firstNoticeTime: row.firstNoticeTime,
					applyRange: row.applyRange,
					registerNoticeNo: row.registerNoticeNo,
					registerNoticeTime: row.registerNoticeTime,
					inlandFlag: row.inlandFlag,
					areaCode: row.areaCode,
					laterAppointTime: row.laterAppointTime,
					expireTime: row.expireTime,
					priorityTime: row.priorityTime,
					internationRegisterTime: row.internationRegisterTime,
					deadlineyear: row.deadlineyear,
					publicFlag: row.publicFlag,
					createTime: row.createTime,
					auditDesc: row.auditDesc,
					customerMobile: row.customerMobile,
				}
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: row.id
					};
					branddelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},

			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					branddelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			}
		},
		computed: {

		},

		components: {

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getProducts();
			// this.configgetList()
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}

	.ck_tab span {
	font-size: 16px;
	}
</style>



