<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> 意见反馈</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.id" placeholder="请输入用户ID"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					意见反馈共：{{products.length}}条
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="products" highlight-current-row v-loading="listLoading"  style="width: 100%;">
			<el-table-column type="index" label="编号" width="90">
			</el-table-column>
			<el-table-column prop="customerId" label="用户ID" min-width="70">
			</el-table-column>
			<el-table-column prop="description" label="联系方式" min-width="70">
			</el-table-column>
			<el-table-column prop="jsonStr" label="反馈内容" min-width="200">
			</el-table-column>
			<el-table-column prop="createTime" label="反馈日期" min-width="130">
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import Vue from 'vue'
	import util from '../../common/js/util'
	import {
		Collectlist,
		Collectdelete,
	} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					customerId: '',
					jsonStr: '',
				},
				editorOption: {},
				products: [],
				total: 0,
				PageNum: 1,
				listLoading: false,
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			//获取产品列表
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
					mobile: this.filters.mobile,
					actionType:'1',
					contentType:'3',
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					this.products = res.data.result.result;
					//console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			getphone() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
					mobile: this.filters.mobile,
					actionType:'1',
					contentType:'3',
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					//this.total = res.data.result.total;
					this.pageNum= res.data.result.pageNum;
					this.listLoading = false;
					this.newproducts = res.data.result.result;
					let childArr = []
					this.newproducts.forEach(value => {
						if (this.filters.id===value.customerId){
							childArr.push(value)
						}
					})
					this.products = childArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					Collectdelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},

		},
		computed: {

		},

		components: {

		},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



