<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> 普通用户</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					普通用户共：{{products.length}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="编号" width="90">
			</el-table-column>
			<el-table-column prop="headLogo" label="头像" width="50">
				<!-- 图片的显示 -->
				<template slot-scope="scope">
					<img :src="scope.row.headLogo" height="30" width="30" />
				</template>
			</el-table-column>
			<el-table-column prop="name" label="用户名称" min-width="70">
			</el-table-column>
			<el-table-column prop="balance" label="账户余额" min-width="70">
			</el-table-column>
			<el-table-column label="编辑账户余额" width="130">
				<template slot-scope="scope">
					<el-button  size="small"  type="primary"  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
				</template>
			</el-table-column>
			<el-table-column prop="email" label="用户邮箱" min-width="120">
			</el-table-column>
			<el-table-column prop="mobile" label="联系方式" min-width="100">
			</el-table-column>
			<el-table-column prop="createTime" label="注册日期" min-width="130">
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--编辑界面-->
		<el-dialog title="编辑账户余额" :visible.sync="editFormVisible">
			<el-form :model="editForm" label-width="160px"  ref="editForm">
				<el-form-item prop="id" label="用户ID">
					<el-input v-model="editForm.id" auto-complete="off" :maxlength="10" placeholder="请输入该用户账户id"></el-input>
				</el-form-item>
				<el-form-item prop="balance" label="用户账户余额(¥)">
					<el-input v-model="editForm.balance" auto-complete="off" :maxlength="10" placeholder="请输入该用户账户余额(¥)"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit()" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20"  :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		getcustomerdDelete,
		getcustomerlist,
		getUseredite
	} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					name: '',
					headLogo: '',
					email: '',
					mobile: ''
				},
				editorOption: {},
				type: [],
				region: [],
				time: '',
				products: [],
				editFormVisible: false, //新增界面是否显示
				editLoading: false,
				total: 0,
				PageNum:1,
				listLoading: false,
				sels: [], //列表选中列
				editForm: {
					balance: '',
				},
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			//获取产品列表
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
				};
				this.listLoading = true;
				//NProgress.start();
				getcustomerlist(para).then((res) => {
					//console.log(res)
					//this.PageNum = res.data.result.PageNum;
					//this.total = res.data.result.total;
					this.oldproducts = res.data.result.result;
					let oldchildArr = []
					this.oldproducts.forEach(value => {
						if ( value.vipFlag == 'N'){
							oldchildArr.push(value)
						}
					})
					this.products = oldchildArr
					this.listLoading = false;
					//NProgress.done();
				});
			},
			getphone() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10
				};
				this.listLoading = true;
				//NProgress.start();
				getcustomerlist(para).then((res) => {
					//this.total = res.data.result.total;
					this.pageNum= res.data.result.pageNum;
					this.oldproducts = res.data.result.result;
					let oldchildArr = []
					this.oldproducts.forEach(value => {
						if (typeof (value.vipFlag) == 'N'){
							oldchildArr.push(value)
						}
					})
					this.newproducts = oldchildArr
					let childArr = []
					this.newproducts.forEach(value => {
						if (this.filters.telephone===value.mobile){
							childArr.push(value)
						}
					})
					this.products = childArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},//显示编辑界面
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//编辑
			editSubmit: function(e) {
				console.log(e)
				this.$refs.editForm.validate((valid) => {
					if(valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							// let para = Object.assign({
							// 	balance: this.editForm.balance,
							// 	id:this.editForm.id
							// }, this.editForm);
							let para = {
								balance: this.editForm.balance,
								customerId:this.editForm.id
							};
							getUseredite(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getProducts();
							});
						});
					}
				});
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					getcustomerdDelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					getcustomerlist(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			}
		},
		computed: {

		},

		components: {

		},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



