<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span>  <i class="el-icon-menu" style="margin-right:3px;"></i>商标问问</span>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">发布文章</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					商标问问共：{{total}}条
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="service" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">

			<el-table-column type="index" label="编号" width="80">
			</el-table-column>
			<el-table-column prop="name" label="文章标题" min-width="250">
			</el-table-column>
			<el-table-column prop="createTime" label="发布时间" min-width="120">
			</el-table-column>
			<el-table-column label="编辑" width="80">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
				</template>
			</el-table-column>
			<el-table-column label="删除" width="80">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--编辑界面-->
		<el-dialog title="编辑文章" :visible.sync="editFormVisible">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item prop="name" label="文章标题">
					<el-input v-model="editForm.name" auto-complete="off" :maxlength="10" placeholder="请输入服务标题"></el-input>
				</el-form-item>
				<el-form-item prop="content" label="文章内容">
					<quill-editor ref="myQuillEditor" v-model="addForm.content" class="editer">
					</quill-editor>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
		<!--新增界面-->
		<el-dialog title="新增文章" :visible.sync="addFormVisible">
			<el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
				<el-form-item prop="name" label="文章标题">
					<el-input v-model="addForm.name" auto-complete="off" :maxlength="20" placeholder="请输入服务标题"></el-input>
				</el-form-item>
				<el-form-item prop="content" label="文章内容">
					<quill-editor ref="myQuillEditor" v-model="addForm.content" class="editer">
					</quill-editor>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import {
		quillEditor
	} from 'vue-quill-editor' //调用编辑器
	import 'quill/dist/quill.core.css'
	import 'quill/dist/quill.snow.css'
	import quillConfig from '../../api/quill-config.js'
	import { servicegetList, servicecreate, serviceupdate, servicedelete, servicegetById } from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					name: '',
					createTime: '',
					content:''
				},
				page: 1,
				total: '',
				service: [],
				listLoading: false,
				bannerUrl: '',
				quillOption: quillConfig,
				editFormVisible: false, //新增界面是否显示
				editLoading: false,
				addFormVisible: false, //新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [{
						required: true,
						message: '请输入文章名称（10字以内）',
						trigger: 'blur'
					}],
					content: [{
						required: true,
						message: '请输入文章内容',
						trigger: 'blur'
					}]
				},
				//新增界面数据
				addForm: [{
					name: '',
					content: '',
				}],
				editFormRules: {
					name: [{
						required: true,
						message: '请输入文章名称（10字以内）',
						trigger: 'blur'
					}],
					content: [{
						required: true,
						message: '请输入服务内容',
						trigger: 'blur'
					}],
				},
				//编辑界面数据
				editForm: {
					name: '',
					content: '',
				},
			}
		},
		methods: {
			imgHandler (handle) {
				this.quill = handle.quill
				var inputfile = document.getElementById('file')
				inputfile.click()
			},
			//获取产品类别列表
			getservice() {
				let para = {
					type: '3',
					pageSize:'30',
				};
				this.listLoading = true;
				servicegetList(para).then((res) => {
					this.service = res.data.result.result;
					this.total = res.data.result.total;
					this.createTime = res.data.result.result.createTime;
					let createTime = this.createTime;
					createTime = new Date(createTime).Format('yyyy.MM.dd');
					this.listLoading = false;
				});
			},
			initFormatter() {
				Date.prototype.Format = function(fmt) { //
					let o = {
						"M+": this.getMonth() + 1, //月份
						"d+": this.getDate(), //日
						"h+": this.getHours(), //小时
						"m+": this.getMinutes(), //分
						"s+": this.getSeconds(), //秒
						"q+": Math.floor((this.getMonth() + 3) / 3), //季度
						"S": this.getMilliseconds() //毫秒
					};
					if(/(y+)/.test(fmt))
						fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
					for(var k in o)
						if(new RegExp("(" + k + ")").test(fmt))
							fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
					return fmt;
				}
			},

			//显示编辑界面
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({},row);
			},
			//显示新增界面
			handleAdd: function() {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					content: '',
				};
			},
			//编辑
			editSubmit: function() {
				this.$refs.editForm.validate((valid) => {
					if(valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({
								name: this.editForm.name,
								content: this.editForm.content,
							}, this.editForm);
							serviceupdate(para).then((res) => {
								//console.log(res)
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getservice();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function() {
				this.$refs.addForm.validate((valid) => {
					if(valid) {
						console.log(valid)
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({
								type: '3',
								name: this.addForm.name,
								content: this.addForm.content,
							}, this.addForm);
							servicecreate(para).then((res) => {
								console.log(res)
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getservice();
							});
						});
					}
				});
			},

			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: row.id
					};
					servicedelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getservice();
					});
				}).catch(() => {

				});
			},
			//图片
			handleoneSuccess(res, file) {
				this.addForm.bannerUrl = 'http://58.87.66.77:80' + res.result
				this.bannerUrl = URL.createObjectURL(file.raw);
			},
			//图片
			handletwoSuccess(res, file) {
				this.editForm.bannerUrl = 'http://58.87.66.77:80' + res.result
				this.bannerUrl = URL.createObjectURL(file.raw);
			},
			beforeAvatarUpload(file) {
				const isJPG = file.type === 'image/jpeg';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if(!isJPG) {
					this.$message.error('上传图片只能是JPG格式!');
				}
				if(!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isJPG && isLt2M;
			},

			beforeediteAvatarUpload(file) {
				this.editForm.bannerUrl = 'http://58.87.66.77:80' + res.result
				const isJPG = file.type === 'image/jpeg';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if(!isJPG) {
					this.$message.error('上传图片只能是JPG格式!');
				}
				if(!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isJPG && isLt2M;
			},
			uploadSuccess(res, file, fileList) {
				this.editData.topicPic = res;
				this.$message.success('上传成功')
			},
			uploadError(err, file) {
				this.$message.error('上传失败')
			},
			onEditorBlur(quill) {
			},
			onEditorFocus(quill) {
			},
			onEditorReady(quill) {
			},
			onEditorChange({
				quill,
				html,
				text
			}) {
				console.log('editor change!', quill, html, text)
				this.content = html
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
		},
		components: {
			//使用编辑器
			quillEditor
		},
		mounted() {
			this.getservice();
			this.initFormatter()
		},
		computed: {
			editor() {
				return this.$refs.myQuillEditor.quill
			}
		},
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



