		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		applylist,
		applydelete,
		applyupdate,
		brandupdate
	} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					sitename: '',
					allprovinces: [],
					allcitys: [],
				},
				//编辑界面数据
				sites: [],
				total: 0,
				page: 1,
				brand_fee : sessionStorage.brand_fee,
				PageNum: 1,
				listLoading: false,
				sels: [], //列表选中列
				editFormVisible: false, //编辑界面是否显示
				editLoading: false,
				editFormRules: {
					sitename: [{
						required: true,
						message: '请输入社区名称（10字以内）',
						trigger: 'blur'
					}],
				},
				//编辑界面数据
				editForm: {
					sitename: '',
					more: '',
					allprovinces: [],
					allcitys: [],
					allareas: [],
				},

				addFormVisible: false, //新增界面是否显示
				addLoading: false,
				addFormRules: {

					sitename: [{
						required: true,
						message: '请输入社区名称（10字以内）',
						trigger: 'blur'
					}],

				},
				//新增界面数据
				addForm: {
					provinces: '',
					city: '',
					area: '',
					sitename: '',
					more: '',
					allprovinces: [],
					allcitys: [],
					allareas: [],
				}
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getorders();
			},
			handleRemove(files) {},
			handlePreview(files) {},
			//获取订单列表
			getorders() {
				let para = {
					applyType: 1,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.sites = res.data.result.result;
					this.total = res.data.result.total;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			getphone() {
				let para = {
					applyType:1,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.newsits = res.data.result.result;
					let childArr = []
					this.newsits.forEach(value => {
						if (this.filters.telephone===value.buyerMobile){
							childArr.push(value)
						}
					})
					this.sites = childArr
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除订单吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					applydelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getorders();
					});
				}).catch(() => {

				});
			},
			shenheSubmit: function(index, row) {
				this.$confirm('确认订单已付款吗?操作不可撤回', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id,
						score:2
					};
					applyupdate(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '审核成功',
							type: 'success'
						});
					let parmas = {
						id: row.id,
						targetJson: {
						status:2
						}
					};
					brandupdate(parmas).then((res) => {
						console.log(res)
						//NProgress.done();
						this.$message({
							message: '商标已售出',
							type: 'success'
						});
					});
						this.getorders();
					});
				}).catch(() => {

				});
			},
				selsChange: function(sels) {
					this.sels = sels;
				},
			},

			mounted() {
				this.getorders();
			}
			}</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



