<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span>  <i class="el-icon-location l-style"></i>服务订单</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					社区共：{{sites.length}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="sites" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column prop="id" label="编号" width="80">
			</el-table-column>
			<el-table-column prop="description" label="服务名称" min-width="130">
			</el-table-column>
			<el-table-column prop="amount" label="交易金额" min-width="70">
			</el-table-column>
			<el-table-column prop="buyerMobile" label="买方联系方式" min-width="100">
			</el-table-column>
			<el-table-column prop="createTime" label="创建时间" min-width="130">
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button style="width: 60px;" type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
			<el-table-column label="管理" width="150">
				<template slot-scope="scope">
					<el-button v-show="scope.row.score =='1'" type="primary" size="small" @click="shenheSubmit(scope.$index, scope.row)">收款确认</el-button>
					<p v-show="scope.row.score =='0'"  size="small" style="color: #666">订单被取消</p>
					<p v-show="scope.row.score =='2'"  size="small" style="color: #0ACA07">确认已收款</p>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		applylist,
		applydelete,
		applyupdate
	} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					description: '',
					amount: '',
					buyerMobile: '',
					status:'',
					description:''
				},
				sites: [],
				total: 0,
				page: 1,
				PageNum: 1,
				listLoading: false,
				sels: [], //列表选中列
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getorders();
			},
			handleRemove(files) {},
			handlePreview(files) {},
			//获取订单列表
			getorders() {
				let para = {
					applyType:0,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.total = res.data.result.total;
					this.sites = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认取消订单吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					applydelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getorders();
					});
				}).catch(() => {

				});
			},
			getphone() {
				let para = {
					applyType:0,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => {
					this.newsits = res.data.result.result;
					let childArr = []
					this.newsits.forEach(value => {
						if (this.filters.telephone===value.buyerMobile){
							childArr.push(value)
						}
					})
					this.sites = childArr
					this.listLoading = false;
					//NProgress.done();
				});
			},
			shenheSubmit: function(index, row) {
				this.$confirm('确认订单已付款吗?操作不可撤回', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id,
						score:2
					};
					applyupdate(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '审核成功',
							type: 'success'
						});
						this.getorders();
					});
				}).catch(() => {

				});
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
		},

		mounted() {
			this.getorders();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



