<template>
	<div class="body">
	<el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-position="left" label-width="0px" class="demo-ruleForm login-container">
			<img src="../assets/logo.png" style="width: 30%; display: block; margin: 0 auto" />
		<h3 class="title">中商标源网后台管理系统</h3>
		<el-form-item prop="loginName">
			<el-input type="text" v-model="ruleForm2.loginName" auto-complete="off" placeholder="账号"></el-input>
		</el-form-item>
		<el-form-item prop="password">
			<el-input type="password" v-model="ruleForm2.password" auto-complete="off" placeholder="密码"></el-input>
		</el-form-item>
		<el-form-item style="width:100%;">
			<el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
		</el-form-item>
	</el-form>
	</div>
</template>

<script>
	import { getUserlogin} from '../api/api';
	//import NProgress from 'nprogress'
	export default {
		data() {
			return {
				logining: false,
				ruleForm2: {
					loginName: '',
					password: ''
				},
				rules2: {
					loginName: [{
							required: true,
							message: '请输入账号',
							trigger: 'blur'
						},
						//{ validator: validaePass }
					],
					password: [{
							required: true,
							message: '请输入密码',
							trigger: 'blur'
						},
						//{ validator: validaePass2 }
					]
				},
				checked: true
			};
		},
		methods: {
			handleSubmit2(ev) {
				var _this = this;
				this.$refs.ruleForm2.validate((valid) => {
							if(valid) {
								this.logining = true;
								let para = {
									loginName: this.ruleForm2.loginName,
									password: this.ruleForm2.password,
								};
								getUserlogin(para).then((res) => {
									if(res.data.code == 0) {
										sessionStorage.loginName = this.ruleForm2.loginName;
										sessionStorage.password = this.ruleForm2.password;
										this.$router.push({
											path: '/'
										});
										this.$message({
											message: '登录成功',
											type: 'seccess'
										});
									} else {
										this.$message({
											message: '请输入用户名密码',
											type: 'warning'
										});
										this.logining = false;
										return false;
									}
								});
							} else {
								this.$Message.error('输入有误');
							}
							})
						}
			}
		}
</script>

<style lang="scss" scoped>
	.body{
		background-color: #1B1BB3;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
	}
	.login-container {
		/*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
		-webkit-border-radius: 5px;
		border-radius: 5px;
		-moz-border-radius: 5px;
		background-clip: padding-box;
		margin: 180px auto;
		width: 350px;
		padding: 35px 35px 15px 35px;
		background: #fff;
		border: 1px solid #eaeaea;
		.title {
			margin: 0px auto 40px auto;
			text-align: center;
			color: #505458;
		}
		.remember {
			margin: 0px 0px 35px 0px;
		}
	}
</style>



