<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span>  <i class="el-icon-menu" style="margin-right:3px;"></i>中商服务</span>
				</el-form-item>
				<el-form-item style="float: right;">
					中商服务共：{{total}}条
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="service" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%; height: 500px; overflow-y: auto;">
			<el-table-column prop="name" label="服务名称" min-width="160">
			</el-table-column>
			<el-table-column prop="bannerUrl" label="服务广告" min-width="160">
				<template slot-scope="scope">
					<img :src="scope.row.bannerUrl" height="36" width="60" />
				</template>
			</el-table-column>
			<el-table-column prop="price" label="价格" min-width="120">
			</el-table-column>
			<el-table-column prop="createTime" label="发布时间" min-width="120">
			</el-table-column>
			<el-table-column label="编辑" width="80">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--编辑界面-->
		<el-dialog title="编辑服务（图片建议40K以内）" :visible.sync="editFormVisible" style="height: 600px;">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm" style="height: 300px; overflow-y: scroll;">
				<el-form-item prop="bannerUrl" label="服务广告">
					<el-upload class="avatar-uploader" :limit="1" action="http://58.87.66.77:80/v1/upload/image" :show-file-list="true" :on-success="handletwoSuccess" :before-upload="beforeediteAvatarUpload">
						<img v-if="editForm.bannerUrl" v-model="editForm.bannerUrl" :src="editForm.bannerUrl" class="avatar">
						<div class="pictext"> 上传414*200<br /> jpg/png格式</div>
					</el-upload>
				</el-form-item>
				<el-form-item prop="name" label="服务标题">
					<el-input v-model="editForm.name" auto-complete="off" :maxlength="10" placeholder="请输入服务标题"></el-input>
				</el-form-item>
				<el-form-item prop="price" label="服务费用">
					<el-input v-model.number="editForm.price" auto-complete="off" :maxlength="10" placeholder="请输入浏览人数"></el-input>
				</el-form-item>
				<el-form-item prop="content" label="服务内容">
					<quill-editor ref="myQuillEditor" :options="quillOption" v-model="editForm.content" class="editer">
					</quill-editor>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>
		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		quillEditor
	} from 'vue-quill-editor' //调用编辑器
	import 'quill/dist/quill.core.css'
	import 'quill/dist/quill.snow.css'
	import quillConfig from '../../api/quill-config.js'
	import { servicegetList, servicecreate, serviceupdate, servicedelete, servicegetById } from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					name: '',
					bannerUrl: '',
					createTime: '',
					price: '',
				},
				total: 0,
				PageNum: '',
				quillOption: quillConfig,
				service: [],
				listLoading: false,
				bannerUrl: '',
				editFormVisible: false, //新增界面是否显示
				editLoading: false,

				editFormRules: {
					name: [{
						required: true,
						message: '请输入服务名称（10字以内）',
						trigger: 'blur'
					}],
					price: [{
						type: 'number',
						message: '请输入服务费用(仅限数字)',
						trigger: 'blur',
					}],
					content: [{
						required: true,
						message: '请输入服务内容',
						trigger: 'blur'
					}],
					bannerUrl: [{
						required: true,
						message: '请上传服务广告',
						trigger: 'blur'
					}],
				},
				//编辑界面数据
				editForm: {
					name: '',
					price: '',
					content: '',
					bannerUrl: '',
				},
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val;
				this.getservice();
			},
			imgHandler (handle) {
				this.quill = handle.quill
				var inputfile = document.getElementById('file')
				inputfile.click()
			},
			//获取产品类别列表
			getservice() {
				let para = {
					type: '1',
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				servicegetList(para).then((res) => {
					this.service = res.data.result.result;
					//console.log(this.service)
					this.total = res.data.result.total;
					this.createTime = res.data.result.result.createTime;
					let createTime = this.createTime;
					createTime = new Date(createTime).Format('yyyy.MM.dd');
					this.listLoading = false;
				});
			},
			initFormatter() {
				Date.prototype.Format = function(fmt) { //
					let o = {
						"M+": this.getMonth() + 1, //月份
						"d+": this.getDate(), //日
						"h+": this.getHours(), //小时
						"m+": this.getMinutes(), //分
						"s+": this.getSeconds(), //秒
						"q+": Math.floor((this.getMonth() + 3) / 3), //季度
						"S": this.getMilliseconds() //毫秒
					};
					if(/(y+)/.test(fmt))
						fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
					for(var k in o)
						if(new RegExp("(" + k + ")").test(fmt))
							fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
					return fmt;
				}
			},

			//显示编辑界面
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function() {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					price: '',
					content: '',
					bannerUrl: '',
				};
			},
			//编辑
			editSubmit: function() {
				this.$refs.editForm.validate((valid) => {
					if(valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({
								name: this.editForm.name,
								price: this.editForm.price,
								content: this.editForm.content,
								bannerUrl: this.editForm.bannerUrl,
							}, this.editForm);
							serviceupdate(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getservice();
							});
						});
					}
				});
			},
			//图片
			handletwoSuccess(res, file) {
				this.editForm.bannerUrl = 'http://58.87.66.77:80' + res.result
				this.bannerUrl = URL.createObjectURL(file.raw);
			},
			beforeediteAvatarUpload(file) {
				const isJPG = file.type === 'image/jpeg';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if(!isJPG) {
					this.$message.error('上传图片只能是JPG格式!');
				}
				if(!isLt2M) {
					this.$message.error('上传图片大小不能超过 2MB!');
				}
				return isJPG && isLt2M;
			},
			uploadSuccess(res, file, fileList) {
				this.editForm.bannerUrl = res;
				this.$message.success('上传成功')
			},
			uploadError(err, file) {
				this.$message.error('上传失败')
			},
			onEditorBlur(quill) {},
			onEditorFocus(quill) {},
			onEditorReady(quill) {},
			onEditorChange({
				quill,
				html,
				text
			}) {
				this.content = html
			},
			selsChange: function(sels) {
				this.sels = sels;
			},
		},
		components: {
			//使用编辑器
			quillEditor
		},
		mounted() {
			this.getservice();
			this.initFormatter()
		},
		computed: {
			editor() {
				return this.$refs.myQuillEditor.quill
			}
		},
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>



