<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> 国内商标</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					专利共：{{products.length}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="编号" width="80">
				<template slot-scope="scope">
					<span v-text="Number(scope.$index) + 1 + (PageNum - 1) * 10"></span>
				</template>
			</el-table-column>
			<el-table-column prop="bannerUrl" label="缩略图" width="130">
				<!-- 图片的显示 -->
				<template slot-scope="scope">
					<img :src="scope.row.bannerUrl" height="40" width="80" style="margin-top: 10px;" />
				</template>
			</el-table-column>
			<el-table-column prop="name" label="专利名称" min-width="130">
			</el-table-column>
			<el-table-column prop="customerMobile" label="联系方式" min-width="180">
			</el-table-column>
			<el-table-column prop="createTime" label="发布时间" min-width="130">
			</el-table-column>
			<el-table-column label="编辑" width="90">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看</el-button>
				</template>
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--编辑界面-->
		<el-dialog title="商标详情" :visible.sync="editFormVisible">
			<el-form :model="editForm">
				<el-row class="ck_tab">
					<el-col :span="24">
						<el-form-item>
							<p><img :src="editForm.bannerUrl" width="414" height="200" style="margin: 0 auto;" /></p>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>专利名称：{{editForm.name}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>价格：{{editForm.price}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>服务费：{{editForm.servicePrice}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>卖家联系方式：{{editForm.customerMobile}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>申请号：{{editForm.applyNo}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>申请时间：{{editForm.createTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>专利分类：{{editForm.categoryFir}}{{editForm.categorySec}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>交易类型：{{editForm.transactionType}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>审核时间：{{editForm.auditTime}}</span>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</el-dialog>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		patentdelete,
		patentgetById,
		patentgetList
	} from '../../api/api';
	export default {

		data() {
			return {
				filters: {
					name: '',
					bannerUrl: '',
					createTime: '',
					customerMobile: '',
				},
				type: [],
				products: [],
				total: 0,
				PageNum:1,
				listLoading: false,
				sels: [], //列表选中列
				editFormVisible: false, //编辑界面是否显示
				//编辑界面数据
				editForm: {
				},

			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			handleRemove(files) {
			},
			handlePreview(files) {
			},
			initFormatter() {
				Date.prototype.Format = function(fmt) { //
					let o = {
						"M+": this.getMonth() + 1, //月份
						"d+": this.getDate(), //日
						"h+": this.getHours(), //小时
						"m+": this.getMinutes(), //分
						"s+": this.getSeconds(), //秒
						"q+": Math.floor((this.getMonth() + 3) / 3), //季度
						"S": this.getMilliseconds() //毫秒
					};
					if(/(y+)/.test(fmt))
						fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
					for(var k in o)
						if(new RegExp("(" + k + ")").test(fmt))
							fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
					return fmt;
				}
			},
			//获取产品列表
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.products = res.data.result.result;
					let createTime =this.products.createTime;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			getphone() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetList(para).then((res) => {
					this.total = res.data.result.total;
					this.pageNum=res.data.result.pageNum;
					this.listLoading = false;
					this.newproducts = res.data.result.result;
					let childArr = []
					this.newproducts.forEach(value => {
						if (this.filters.telephone===value.customerMobile){
							childArr.push(value)
						}
					})
					this.products = childArr
					console.log(this.products)
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//显示编辑界面
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = {
					name: row.name,
					price: row.price,
					servicePrice: row.servicePrice,
					applyNo: row.applyNo,
					customerMobile: row.customerMobile,
					bannerUrl: row.bannerUrl,
					createTime: row.createTime,
					categorySec: row.categorySec,
					transactionType: row.transactionType,
					auditTime: row.auditTime,
				}
				console.log(row)
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: row.id
					};
					patentdelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},

			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					patentdelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			}
		},
		computed: {

		},

		components: {

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getProducts();
			this.initFormatter();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}

	.ck_tab span {
		font-size: 16px;
	}
</style>



