<template>
	<div>
		<h1 style="margin-bottom: -20px;">用户活跃度概览</h1>
	<el-row :gutter="40" class="panel-group">
			<el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
				<div class="card-panel" @click="handleSetLineChartData('newVisitis')">
					<div class="card-panel-icon-wrapper icon-people">
						<i class="el-icon-user-solid"></i>
					</div>
					<div class="card-panel-description">
						<div class="card-panel-text">
							会员总人数
						</div>
						<count-to :start-val="0" :end-val="102400" :duration="2600" class="card-panel-num" />
					</div>
				</div>
			</el-col>
			<el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
				<div class="card-panel" @click="handleSetLineChartData('messages')">
					<div class="card-panel-icon-wrapper icon-message"> 
						<i class="el-icon-s-goods"></i>
					</div>
					<div class="card-panel-description">
						<div class="card-panel-text">
							平台商标总量
						</div>
						<count-to :start-val="0" :end-val="81212" :duration="3000" class="card-panel-num" />
					</div>
				</div>
			</el-col>
			<el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
				<div class="card-panel" @click="handleSetLineChartData('purchases')">
					<div class="card-panel-icon-wrapper icon-money">
						<i class="el-icon-s-grid"></i>
					</div>
					<div class="card-panel-description">
						<div class="card-panel-text">
							平台求购总量
						</div>
						<count-to :start-val="0" :end-val="9280" :duration="3200" class="card-panel-num" />
					</div>
				</div>
			</el-col>
			<el-col :xs="12" :sm="12" :lg="6" class="card-panel-col">
				<div class="card-panel" @click="handleSetLineChartData('shoppings')">
					<div class="card-panel-icon-wrapper icon-shopping">
						<i class="el-icon-tickets"></i>
					</div>
					<div class="card-panel-description">
						<div class="card-panel-text">
							订单总量
						</div>
						<count-to :start-val="0" :end-val="13600" :duration="3600" class="card-panel-num" />
					</div>
				</div>
			</el-col>
		</el-row>

	<section class="chart-container">
		<el-row>
			<el-col :span="12">
				<div id="chartBar" style="width:100%; height:400px;"></div>
			</el-col>
			<el-col :span="12">
				<div id="chartPie" style="width:100%; height:400px;"></div>
			</el-col>
		</el-row> 
	</section>
</div>
</template>

<script>
	import echarts from 'echarts'
	import CountTo from 'vue-count-to'
	export default {
		data() {
			return {
				chartBar: null,
				chartLine: null,
				chartPie: null
			}
		},
		components: {
			CountTo
		},
		methods: {
			handleSetLineChartData(type) {
				this.$emit('handleSetLineChartData', type)
			},
			drawBarChart() {
				this.chartBar = echarts.init(document.getElementById('chartBar'));
				this.chartBar.setOption({
					title: {
						text: '用户增长量',
						subtext: '近半年'
					},
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						}
					},
					legend: {
						data: ['普通', 'VIP']
					},
					grid: {
						left: '3%',
						right: '4%',
						bottom: '3%',
						containLabel: true
					},
					xAxis: {
						type: 'value',
						boundaryGap: [0, 0.01]
					},
					yAxis: {
						type: 'category',
						data: ['1月', '2月', '3月', '4月', '5月', '6月']
					},
					series: [{
							name: '普通',
							type: 'bar',
							data: [182, 234, 290, 1049, 1317, 6302]
						},
						{
							name: 'VIP',
							type: 'bar',
							data: [193, 234, 310, 1215, 1341, 6817]
						}
					]
				});
			}, 
			drawPieChart() {
				this.chartPie = echarts.init(document.getElementById('chartPie'));
				this.chartPie.setOption({
					title: {
						text: '订单总数分析',
						subtext: '全部',
						x: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "{a} <br/>{b} : {c} ({d}%)"
					},
					legend: {
						orient: 'vertical',
						left: 'left',
						data: ['商标交易', '专利交易', '求购交易', '商标服务', 'VIP会员升级']
					},
					series: [{
						name: '访问来源',
						type: 'pie',
						radius: '55%',
						center: ['50%', '60%'],
						data: [{
								value: 335,
								name: '商标交易'
							},
							{
								value: 310,
								name: '专利交易'
							},
							{
								value: 234,
								name: '求购交易'
							},
							{
								value: 135,
								name: '商标服务'
							},
							{
								value: 1548,
								name: 'VIP会员升级'
							}
						],
						itemStyle: {
							emphasis: {
								shadowBlur: 10,
								shadowOffsetX: 0,
								shadowColor: 'rgba(0, 0, 0, 0.5)'
							}
						}
					}]
				});
			},
			drawCharts() {
				this.drawBarChart() 
				this.drawPieChart()
			},
		},

		mounted: function() {
			this.drawCharts()
		},
		updated: function() {
			this.drawCharts()
		}
	}
</script>

<style lang="scss"  scoped>
	.chart-container {
		width: 100%;
		float: left;
	}
	/*.chart div {
        height: 400px;
        float: left;
    }*/
	
	.el-col {
		padding: 30px 20px;
	}
	
	.panel-group {
		margin-top: 18px;
		.card-panel-col {
			margin-bottom: 32px;
		}
		.card-panel {
			height: 108px;
			cursor: pointer;
			font-size: 36px;
			position: relative;
			overflow: hidden;
			color: #666;
			background: #fff;
			box-shadow: 4px 4px 40px rgba(0, 0, 0, .05);
			border-color: rgba(0, 0, 0, .05);
			&:hover {
				.card-panel-icon-wrapper {
					color: #fff;
				}
				.icon-people {
					background: #40c9c6;
				}
				.icon-message {
					background: #36a3f7;
				}
				.icon-money {
					background: #f4516c;
				}
				.icon-shopping {
					background: #34bfa3
				}
			}
			.icon-people {
				color: #40c9c6;
			}
			.icon-message {
				color: #36a3f7;
			}
			.icon-money {
				color: #f4516c;
			}
			.icon-shopping {
				color: #34bfa3
			}
			.card-panel-icon-wrapper {
				float: left;
				margin: 14px 0 0 14px;
				padding: 16px;
				transition: all 0.38s ease-out;
				border-radius: 6px;
				background-color: #f6f6f6;
			}
			.card-panel-icon {
				float: left;
				font-size: 48px;
			}
			.card-panel-description {
				float: left;
				font-weight: bold;
				margin: 20px; 
				.card-panel-text {
					line-height: 18px;
					color: rgba(0, 0, 0, 0.45);
					font-size: 16px;
					margin-bottom: -10px;
				}
				.card-panel-num {
					font-size: 20px;
				}
			}
		}
	}
	
	@media (max-width:550px) {
		.card-panel-description {
			display: none;
		}
		.card-panel-icon-wrapper {
			float: none !important;
			width: 100%;
			height: 100%;
			margin: 0 !important;
			.svg-icon {
				display: block;
				margin: 14px auto !important;
				float: none !important;
			}
		}
	}
</style>


