<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> 国内商标</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getphone">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					专利求购共：{{products.length}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="编号" width="80">
				<template slot-scope="scope">
					<span v-text="Number(scope.$index) + 1 + (PageNum - 1) * 10"></span>
				</template>
			</el-table-column>
			<el-table-column prop="title" label="商标名称" min-width="130">
			</el-table-column>
			<el-table-column prop="budget" label="预算" min-width="130">
			</el-table-column>
			<el-table-column prop="description" label="联系方式" min-width="180">
			</el-table-column>
			<el-table-column prop="createTime" label="发布时间" min-width="130">
			</el-table-column>
			<el-table-column label="编辑" width="90">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">查看</el-button>
				</template>
			</el-table-column>
			<el-table-column label="删除" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--编辑界面-->
		<el-dialog title="商标详情" :visible.sync="editFormVisible">
			<el-form :model="editForm">
				<el-row class="ck_tab">
					<el-col :span="12">
						<el-form-item>
							<span>求购名称：{{editForm.title}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>预算：{{editForm.budget}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>发布日期：{{editForm.createTime}}</span>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span>所属分类：{{editForm.jsonStr}}</span>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</el-dialog>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		Collectlist,
		Collectdelete,
	} from '../../api/api';
	export default {

		data() {
			return {
				filters: {
					name: '',
					bannerUrl: '',
					createTime: '',
					customerMobile: '',
				},
				type: [],
				products: [],
				total: 0,
				PageNum:1,
				listLoading: false,
				sels: [], //列表选中列
				editFormVisible: false, //编辑界面是否显示
				//编辑界面数据
				editForm: {
				},

			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			handleRemove(files) {
			},
			handlePreview(files) {
			},
			//获取产品列表
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
					actionType:'1',
					contentType:'2'
				};
				this.listLoading = true;
				//NProgress.start();
				Collectlist(para).then((res) => {
					//console.log(res.data.result);
					this.total = res.data.result.total;
					this.products = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
            getphone() {
                let para = {
                    pageNum: this.pageNum,
                    pageSize: 10,
                    actionType:'1',
                    contentType:'2'
                };
                this.listLoading = true;
                //NProgress.start();
                Collectlist(para).then((res) => {
                    //console.log(res.data.result);
                    this.total = res.data.result.total;
                    this.pageNum=res.data.result.pageNum;
                    this.listLoading = false;
                    this.newproducts = res.data.result.result;
                    let childArr = []
                    this.newproducts.forEach(value => {
                        if (this.filters.telephone===value.description){
                            childArr.push(value)
                        }
                    })
                    this.products = childArr
                    console.log(this.products)
                    this.listLoading = false;
                    //NProgress.done();
                });
            },
			//显示编辑界面
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = {
					title: row.title,
					budget: row.budget,
					jsonStr: row.jsonStr,
					createTime: row.createTime,
					customerMobile: row.customerMobile,
				}
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: row.id
					};
					Collectdelete(para).then((res) => {
						if(res.data.result !== false){
							this.listLoading = false;
							//NProgress.done();
							this.$message({
								message: '删除成功',
								type: 'success'
							});

						}else{
							this.$message({
								message: res.data.message,
								type: 'warning'
							});
						}
						this.getProducts();
					});
				}).catch(() => {

				});
			},

			selsChange: function(sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function() {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					branddelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			}
		},
		computed: {

		},

		components: {

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getProducts();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}

	.ck_tab span {
	font-size: 16px;
	}
</style>



